import eventManager from '../core/manager/events';
import CellsRouter from '../core/router';

import { EVENTS, PREFIX } from './constants';

/**
 * Class to manage stack of routes and navigate
 * @extends CellsRouter
 */
let CellsNativeRouter = class CellsNativeRouter extends CellsRouter {

  constructor() {
    super();
    this.init();
  }


  /**
   * Initialize router stack.
   * Clear stack and add first native route
   */
  init() {
    const initialRoute = this.navigationStack.createRoute(`${PREFIX.NATIVE_ROUTE}init`);
    this._clearStack();
    this.navigationStack.push(initialRoute);
  }

  /**
   * Add or replace a route to the route stack.
   *
   * @param {Object} route - Route object.
   * @param {Boolean} replace - Indicates if given route will replace last route.
   * @param {Boolean} native - Indicates if given route is native.
   */
  manageStack(route, replace = false, native = false) {
    const navigation = {};
    const name = route.page;
    const { options = {} } = route;
    const lastRouteName = (this.getLastRoute() || {}).page;
    const cleanNavigationStack = options.cleanNavigationStack || false;
    const cleanUntil = options.cleanUntil || false;

    navigation.from = this.getLastRoute();
    navigation.to = route;
    if (name !== lastRouteName) {
      const path = (native ? PREFIX.NATIVE_ROUTE : '') + name;
      const routeToAppend = Object.assign({}, route, { page: path });

      if (cleanNavigationStack) {
        this._clearStack();
      } else if (cleanUntil) {
        this.clearStackUntil(cleanUntil);
      }

      //options are not usefull inside route stack
      delete routeToAppend.options;

      if (replace) {
        this.navigationStack.replaceRoute(routeToAppend);
      } else {
        this.navigationStack.push(routeToAppend);
      }
    } else {
      console.log("manageStack::UPDATE PRIVATE CHANNEL. FROM WEB TO NATIVE. WEB PAGE MUST BE ????: ", lastRouteName, '->', name);
    }

    return navigation;
  }

  addSkipNavigation(nav) {
    console.log('Router:addSkipNavigation', nav);
    this.navigationStack.addSkipNavigation(nav);
  }

  _wait(pauseInMiliseconds) {
    const startTime = new Date();
    let elapsedTime = null;
    do {
      elapsedTime = new Date();
    } while(elapsedTime-startTime < pauseInMiliseconds);
  }

  /**
   * Perform a back operation over the router stack and emit and event and/or navigate to corresponding page.
   *
   * @emits NATIVE_BACKSTEP_REQUEST
   * @emits INTERNAL_ROUTER_BACKSTEP_FROM_NATIVE
   */
  back() {
    const navigation = {};
    if (this.navigationStack.length > 1) {
      let fromRoute = this.navigationStack.pop();
      let auxFromRoute = fromRoute;
      let backRoute = this.getLastRoute();

      console.log('back::', fromRoute, backRoute);

      while (this.navigationStack.isSkipNavigation({from: auxFromRoute.page, to: backRoute.page}) && this.navigationStack.length>1) {
        console.log('back::skipped -> ', auxFromRoute, backRoute);
        auxFromRoute = this.navigationStack.pop();
        if (this.navigationStack.length > 0) {
          backRoute = this.getLastRoute();
        }
      }

      const isCommingBackFromNativeRoute = this.isNativeRoute(fromRoute);
      const isGoingBackToNativeRoute = this.isNativeRoute(backRoute);
      const page = backRoute.page.replace(PREFIX.NATIVE_ROUTE, '');
      const params = backRoute.params;

      navigation.from = fromRoute;
      navigation.to = backRoute;

      if (isGoingBackToNativeRoute) {
        eventManager.emit(EVENTS.NATIVE_BACKSTEP_REQUEST, { page });
      } else if (isCommingBackFromNativeRoute) {
        eventManager.emit(EVENTS.INTERNAL_ROUTER_BACKSTEP_FROM_NATIVE, { page });
      }

      if (!isGoingBackToNativeRoute) {
        super.go(page, params);
      } else {
        this._wait(800);
      }
    } else {
      navigation.from = this.getLastRoute();
      navigation.to = this.getLastRoute();
    }
    return navigation;
  }

  /**
   * Check if given route is a native route.
   *
   * @param {Object} route - Route to check
   * @returns {Boolean}
   */
  isNativeRoute(route) {
    const { page } = route;

    return page && page.startsWith(PREFIX.NATIVE_ROUTE);
  }

  invalidateLocationHash() {
    this.locationHash("");
  }

  locationHash(path) {
    console.log('locationHash: ', path);
    Promise.resolve().then( () =>
      setTimeout( () => {
        console.log('pr locationHash: ', path);
        location.hash = '#!' + path
      }, 100));
  }

  createRoute(page, params = {}) {
    return {
      page,
      params
    }
  }
}

export default CellsNativeRouter;
