import PolymerTemplateManager from '../../polymer/manager/template';

/**
 * Class to manage cells templates
 * @extends PolymerTemplateManager
 */
let CellsNativeTemplateManager = class CellsNativeTemplateManager extends PolymerTemplateManager {

  /**
   * Search selected template in cache array and cache it
   */
  cacheSelected() {
    const template = this.cache[this.selected];
    const cache = this.cache;

    if (template) {
      for (let tplName in cache) {
        if(cache.hasOwnProperty(tplName)){
          if (tplName !== template.name) {
            cache[tplName].cache();
            cache[tplName].resetNextNavigation();
          }
        }
      }
      this.selected = null;
      template.native();
    }
  }

  /**
   * Select a template from the cache
   * @param {String} name - Temaplate name to select
   */
  selectTemplate(name) {
    const template = this.cache[name];
    if (template && (template.node.getAttribute('state') === 'native')) {
      this.selected = name;
      template.activate();
    }
  }
}

export default CellsNativeTemplateManager;
