import CellsPolymerComponentConnector from '../polymer/component-connector';
import eventManager from '../core/manager/events';

import { EVENTS, PREFIX } from './constants';

const PRIVATE_EVENTS = EVENTS.PRIVATE;

/**
 * Class to connect components throuhg the Pub/Sub
 * @extends CellsPolymerComponentConnector
 */
let CellsNativeComponentConnector = class CellsNativeComponentConnector extends CellsPolymerComponentConnector {

  handleI18n(cb) {
    this.manager.get(`${this.getNativeEventChannelPrefix()}locales`).subscribe(cb);
  }

  handleConfig(cb) {
    this.manager.get(`${this.getNativeEventChannelPrefix()}config`).subscribe(cb);
  }

  handleAppLoad(cb) {
    this.manager.get(`${this.getNativeEventChannelPrefix()}app_loaded`).subscribe(cb);
  }

  handleAppContextChange(cb) {
    this.manager.get(this.getAppContextChannelName()).subscribe(cb);
  }

  getNativeEventChannelPrefix() {
    return '__native_bridge_evt_';
  }

  getAppContextChannelName() {
    return '__bridge_app';
  }

  /**
   * Get all valid public channels from bridge channel manager.
   *
   * @return {Channel[]} Array of public channels with valid value.
   */
  getChannels() { //@TODO: getPublicChannels

    const publicChannels = this._getPublicChannels(this.manager.channels);
    return publicChannels.filter(this._isValidChannel.bind(this));
  }

  getChannel(channelName) {

    return this._getChannel(channelName, this.manager.channels);
  }

  /**
   * Get all valid channels from bridge channel manager.
   *
   * @return {Channel[]} Array of channels with valid value.
   */
  getAllChannels() {

    const channels = this._getChannels(this.manager.channels);

    return channels.filter(this._isValidChannel.bind(this));
  }

  /**
   * Get all channels from given channels string array.
   *
   * @param  {Array} channels Channel names array.
   * @return {Array}          Channels object array.
   */
  _getChannels(channels) {
    const channelNames = Object.keys(channels);

    return channelNames.map((channelName) => this._getChannel(channelName, channels));
  }

  /**
   * Get all public channels from given channels string array.
   *
   * @param  {Array} channels Channel names array.
   * @return {Array}          Channels object array.
   */
  _getPublicChannels(channels) {
    const channelNames = Object.keys(channels);
    const publicChannels = channelNames.filter(this._isPublicChannel);

    return publicChannels.map((channelName) => this._getChannel(channelName, channels));
  }

  /**
   * Decides if given channel name is a public channel.
   *
   * @param  {String}  channelName Name of channel.
   * @return {Boolean}
   */
  _isPublicChannel(channelName) {
    return !channelName.startsWith(PREFIX.PRIVATE_CHANNEL);
  }

  /**
   * Get channel object based on given channel name and channels array.
   *
   * @param  {String} channelName Channel name.
   * @param  {Array} channels     Array of channels.
   * @return {Object}             Channel object.
   */
  _getChannel(channelName, channels) {
    const channel = channels[channelName];

    return {
      name: channelName,
      payload: this._getChannelPayload(channel)
    };
  }

  /**
   * Gets payload from given channel.
   *
   * @param  {Object} channel Channel objet.
   * @return {Object}         Channel payload.
   */
  _getChannelPayload(channel) {
    return channel._events[0] && channel._events[0].detail;
  }

  /**
   * Decides if given channel object is a valid channel: Has payload, has content, and is not an HTML Element.
   *
   * @param  {Object}  channel Channel object.
   * @return {Boolean}
   */
  _isValidChannel(channel) {
    return this._hasPayload(channel) && !this._hasForbiddenPayload(channel) && this._hasContent(channel) && !this._isHTMLElement(channel);
  }

  /**
   * Decides if given channel object has payload.
   *
   * @param  {Object}  channel Channel object.
   * @return {Boolean}
   */
  _hasPayload(channel) {
    return channel.payload;
  }

  /**
   * Decides if given channel object has content.
   *
   * @param  {Object}  channel Channel object.
   * @return {Boolean}
   */
  _hasContent(channel) {
    return !channel.payload.constructor === Object || Object.keys(channel.payload).length > 0;
  }

  /**
   * Decides if given object has any forbidden properties inside payload.
   * Example: 'sourceEvent', 'preventer', from a click event, that handles to
   * circular reference excepction while doing data sync.
   *
   * @param  {Object}  channel Object to analyze.
   * @return {Boolean}
   */
  _hasForbiddenPayload(channel) {
    const FORBIDDEN_PAYLOAD_PROPERTIES = ['sourceEvent', 'preventer'];
    const properties = Object.keys(channel.payload);

    return Array.prototype.includes.apply(properties, FORBIDDEN_PAYLOAD_PROPERTIES);
  }

  /**
   * Decides if given object is an HTML Element.
   *
   * @param  {Object}  detail Object to analyze.
   * @return {Boolean}
   */
  _isHTMLElement(channel) {
    return channel.payload.node || channel.payload instanceof HTMLElement;
  }

  /**
   * Run through all given channels and performs a sync operation in the Pub/Sub.
   *
   * @param {Array} channels Array of channels name to sync.
   */
  syncChannels(channels) {
    channels.forEach(this._syncChannel.bind(this));
  }

  /**
   * Run through all channels and performs a sync operation in the Pub/Sub.
   *
   * @param {Array} channels Array of channels name to sync.
   */
  syncAllChannels() {
    this.getAllChannels().forEach(this._syncChannel.bind(this));
  }

  /**
   * Recovers channel observer object from manager based on channel name and fires a SYNC_CHANNELS_DATA event.
   *
   * @param  {String} channelName Channel name to sync.
   */
  _syncChannel(channel) {
    const channelName = channel.name;
    const detail = channel.payload;
    const observer = this.manager.get(channelName);

    observer.next(new CustomEvent(PRIVATE_EVENTS.SYNC_CHANNELS_DATA, { detail }));
  }

  /**
   * Function called by base component connector allowing extension of json events.
   *
   * @param {Event} event
   * @param {Connection} connection - Connection to check
   */
  wrapEventHandler(event, connection, channel) {
    if (!connection) {
      return;
    }

    if (connection.nativeLink) {
      eventManager.emit(EVENTS.NATIVE_NAVIGATION_REQUEST, {
        event: event,
        detail: connection.nativeLink
      });
    }

    if (connection.hasOwnProperty('forward-to-native')) {
      eventManager.emit(EVENTS.NATIVE_CHANNEL_SYNC, {
        event: event,
        detail: channel.name
      });
    }
  }
}

export default CellsNativeComponentConnector;
