import eventManager from '../../core/manager/events';

import { EVENTS, OPERATIONS } from '../constants';

/**
 * Class to execute native operations
 */
let NativeOperationManager = class NativeOperationManager {

  /**
   * Execute a native operation
   *
   * @param {String} operation - operation name (must match with defined in constants)
   * @param {Object} payload - payload of the operation
   * @emits ROUTER_BACKSTEP
   * @emits ROUTER_NAVIGATION
   * @emits SYNC_CHANNELS_DATA
   */
  execute(operation, payload) {
    const { SYNC_CHANNELS_DATA, NATIVE: { BACK_STEP, NAVIGATION, RESET_WEBVIEW,
      LOGOUT, UPDATE_SKIP_NAVIGATIONS } } = OPERATIONS;

    if (!operation) {
      return;
    }

    if (operation === SYNC_CHANNELS_DATA) {
      this._executeSyncChannelsData(payload);
    } else if (operation === BACK_STEP) {
      this._executeBackStep(payload);
    } else if (operation === NAVIGATION) {
      this._executeNavigation(payload);
    } else if (operation === RESET_WEBVIEW) {
      this._executeResetWebview();
    } else if (operation === LOGOUT) {
      this._executeLogout();
    } else if (operation === UPDATE_SKIP_NAVIGATIONS) {
      this._executeUpdateSkipNavigations(payload);
    } else {
      console.log(`Unkown operation received from native message: ${JSON.stringify({operation, payload})}`);
    }
  }

  /**
   * Gets channels from given payload and emits a SYNC_CHANNELS_DATA event.
   * @private
   * @param  {Object} payload Object containing channels to sync.
   */
  _executeSyncChannelsData(payload) {
    const channels = this._getChannelsFromPayload(payload);

    eventManager.emit(EVENTS.SYNC_CHANNELS_DATA, { channels });
  }

  /**
   * Extracts channels from given payload, normalizes them on an array,
   * and maps them to desired format.
   * @private
   * @param  {Object} payload Object containing channels data.
   * @return {Array}          Array of channels.
   */
  _getChannelsFromPayload(payload) {
    let channelsData = payload.channels;

    if (!Array.isArray(channelsData)) {
      channelsData = [channelsData];
    }

    return channelsData.map(this._mapChannel);
  }

  /**
   * Map given channel object to needed bridge channel format.
   * @private
   * @param  {String} channelName    Channel name.
   * @param  {Object} channelPayload Channel payload.
   *
   * @return {Object}                Channel object in desired format.
   */
  _mapChannel({channelName, channelPayload}) {
    return {
      name: channelName,
      payload: channelPayload
    };
  }

  /**
   * Emites a ROUTER_BACKSTEP event with given payload.
   * @private
   * @param  {Object} payload Operation payload.
   */
  _executeBackStep(payload) {
    eventManager.emit(EVENTS.ROUTER_BACKSTEP, payload);
  }

  /**
   * Emits a ROUTER_NAVIGATION event with given payload.
   * @private
   * @param  {Object} payload Operation payload.
   */
  _executeNavigation(payload) {
    eventManager.emit(EVENTS.ROUTER_NAVIGATION, payload);
  }

  /**
   * Emits a RESET_WEBVIEW event.
   */
  _executeResetWebview() {
    eventManager.emit(EVENTS.RESET_WEBVIEW);
  }

  /**
   * Emits a Logout event.
   */
  _executeLogout(){
    eventManager.emit(EVENTS.NATIVE_LOGOUT);
  }

  /**
   * Updates
   *
   * @param {Object} payload contains navigations to add to (or remove from) skipNavigations
   */
  _executeUpdateSkipNavigations(payload) {
    eventManager.emit(EVENTS.UPDATE_SKIP_NAVIGATIONS, payload);
  }
}

export default NativeOperationManager;
