import { KEYS, PLUGIN_RESPONSE_STATUS, PLUGIN_RESPONSE_CALLBACKS } from '../constants';

/**
 * Class to manage communication with native plugins
 */
let CellsNativePluginManager = class CellsNativePluginManager {
  _callbacks = {};

  /**
   * Reference to Native Plugin JS Interface inside window.
   *
   * @return {Object} Plugin JS Interface.
   */
  get source() {
    return window[KEYS.NATIVE_PLUGIN_CHANNEL];
  }

  /**
   * Initialize Plugin Manager
   */
  constructor() {
    console.log('CellsPluginManager()');

    this.init();
  }

  init() {
    this._prepareCellsNativeBridgeNameSpace();
  }

  /**
   *
   * Prepares cells native bridge namespace inside windows object, exposing only
   * needy functions from plugin manager.
   * @private
   */
  _prepareCellsNativeBridgeNameSpace() {
    window.CellsNativePlugins = window.CellsNativePlugins || {};
    window.CellsNativePlugins.pluginManager = {
      execute: this._executePlugin.bind(this),
      pluginResponse: this._processPluginResponse.bind(this)
    };
  }

  /**
   * Executes a native plugin with given configuration.
   * For this, we create a unique PID and store the plugin execution with given
   * callbacks for later execution including native plugin result.
   * Then, it creates a execute plugin message with previous PID to send over the native bridge.
   * @private
   * @param  {Function}   success Success callback to be invoked after native plugin execution (if it's a success result)
   * @param  {Function}   fail    Fail callback to be invoked after native plugin execution (if it's a fail result)
   * @param  {String}     plugin  Plugin to be invoked.
   * @param  {String}     action  Plugin method to invoke.
   * @param  {Object}     payload Payload to be sent on plugin invocation.
   */
  _executePlugin(success, fail, plugin, action, payload) {
    const pluginPID = this._createUUID();
    const pluginMessage = this._createPluginMessage(pluginPID, plugin, action, payload);

    this._storeCallback(pluginPID, success, fail);
    this._sendMessage(pluginMessage);
  }

  /**
   * Executes success or failure plugin callback based on associated stored plugin invocation.
   * Deletes associated plugin execution callbacks.
   * @private
   * @param  {String}         callbackId  Executed plugin ID
   * @param  {String|Object}  response    String to be deserialized or raw object containing status and payload based on plugin execution result.
   */
  _processPluginResponse(callbackId, response) {
    const { SUCCESS } = PLUGIN_RESPONSE_STATUS;
    const { SUCCESS_ACCESSOR, FAIL_ACCESSOR } = PLUGIN_RESPONSE_CALLBACKS;
    const result = this._normalizePluginResponse(response);
    const { status, payload } = result;
    const accessor = status == SUCCESS ? SUCCESS_ACCESSOR : FAIL_ACCESSOR;
    const callback = this._callbacks[callbackId][accessor];

    callback(payload);

    delete this._callbacks[callbackId];
  }

  /**
   * Normalizes plugin response based on it's type.
   * @private
   * @param  {String|Object}  response    String to be deserialized or raw object containing status and payload based on plugin execution result.
   * @return {Object}         Plugin response object. Contains {status, payload}.
   */
  _normalizePluginResponse(response) {
    return typeof(response) === 'string' ? JSON.parse(response) : response;
  }

  /**
   * Creates a random v4 UUID.
   * Taken from https://stackoverflow.com/questions/105034/create-guid-uuid-in-javascript/2117523#2117523
   * @private
   * @return {String} v4 random UUID.
   */
  _createUUID() {
    return ([1e7]+-1e3+-4e3+-8e3+-1e11).replace(/[018]/g, c =>
      (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
    );
  }

  /**
   * Creates a plugin message object based on given parameters.
   * @private
   * @param  {String} id      Plugin execution unique proccess identifier.
   * @param  {String} plugin  Plugin name.
   * @param  {String} action  Plugin method to be invoked.
   * @param  {Object} payload Payload to send on plugin method invocation.
   * @return {Object}         Object containing information needed to execute a native plugin.
   */
  _createPluginMessage(id, plugin, action, payload) {
    return {
      id,
      pluginId: plugin,
      actionId: action,
      payload: payload
    };
  }

  /**
   * Stores given success and fail callbacks as properties on an object inside
   * _callbacks with given pluginPID as key for later retrieval.
   * @private
   * @param  {String}   pluginPID Plugin execution unique proccess identifier.
   * @param  {Function} success   Success callback to be invoked after native plugin execution (if it's a success result)
   * @param  {Function} fail      Fail callback to be invoked after native plugin execution (if it's a fail result)
   */
  _storeCallback(pluginPID, success, fail) {
    this._callbacks[pluginPID] = {
      success,
      fail
    };
  }

  /**
   * Send given message over the bridge.
   * @private
   * @param  {Object} message Message object containing information needed to execute a native plugin.
   */
  _sendMessage(message) {
    this.source.postMessage(JSON.stringify(message));
  }
};

export default CellsNativePluginManager;
