import BridgeChannelManager from '../../core/manager/bridge-channels';

let NativeBridgeChannelManager = class NativeBridgeChannelManager extends BridgeChannelManager {

  /**
   * Initializes the private chanel for the given page.
   *
   * @param {String} pageName
   *
   */
  initPrivateChannel(oldPageName, newPageName) {
    const normalizedOldPageName = oldPageName||'cells-native-route:init';
    const normalizedNewPageName = newPageName||'cells-native-route:init';
    if (normalizedOldPageName === normalizedNewPageName) {
      return;
    }
    if (this.appState &&
        this.appState.from === normalizedOldPageName &&
        this.appState.to === normalizedNewPageName) {
      return;
    }
    super.initPrivateChannel(oldPageName, newPageName);

    this.appState = {from: normalizedOldPageName, to: normalizedNewPageName};
  }

}

/**
 * NativeBridgeChannelManager definition
 */
export default NativeBridgeChannelManager;