import NativeOperationManager from './manager/operation';
import CellsNativePluginManager from './manager/plugin';

import { OPERATIONS, KEYS } from './constants';

/**
 * Class to manage native  in/out connections
 */
let CellsNativeManager = class CellsNativeManager {
  /**
   * Get the source to communicate with native bridge
   *
   * @return {Object} The source.
   */
  get source() {
    return window[KEYS.NATIVE_CHANNEL];
  }

  /**
   * Operation manager to execute native operations
   * @type {NativeOperationManager}
   */
  operationManager = null;

  /**
   * Plugin manager to execute and orchestrate plugin communication.
   * @type {CellsNativePluginManager}
   */
  pluginManager = null;

  /**
   * Initialize operationManager
   */
  constructor() {
    console.log('CellsNativeManager()');

    this.init();
  }

  /**
   * Attach listeners to listen events from native
   */
  init() {
    console.log(`CellsNativeBridge::init(${name})`);

    this.operationManager = new NativeOperationManager();
    this.pluginManager = new CellsNativePluginManager();

    this._attachEventListeners();
  }

  _attachEventListeners() {
    window.addEventListener('message', this._processMessage.bind(this));
  }

  /**
   * HTML -> NATIVO
   */

  /**
   * Send to native bridge a message to navigate to page passed
   * @param {String} pageName   - Name of the page to navigate
   * @param {Object} pageParams - Params of the page to navigate
   * @param {Boolean} replace   - Replace navigation stack with given page
   */
  sendNavigationMessage(pageName, pageParams, replace = false) {
    console.log(`CellsNativeManager::sendNavigationMessage(${pageName}, ${pageParams}, ${replace})`);
    const navigationMessage = this._createNavigationMessage(pageName, pageParams, replace);

    this._sendMessage(navigationMessage);
  }

  /**
   * Send to native bridge a message to perform a back operation
   */
  sendBackstepMessage() {
    console.log(`CellsNativeManager::sendBackstepMessage()`);
    const backStepMessage = this._createBackStepMessage();

    this._sendMessage(backStepMessage);
  }

  /**
   * Send to native bridge a message to sync channels information
   * @param {Channel[]} channelsData - Array of channel data
   */
  sendSyncChannelsDataMessage(data) {
    console.log('CellsNativeManager::sendSyncChannelsDataMessage with data = ', data);

    const channels = data.map(this._mapChannel);
    const syncChannelsDataMessage = this._createSyncChannelsDataMessage(channels);

    this._sendMessage(syncChannelsDataMessage);
  }

  sendBridgeReadyMessage() {
    const bridgeReadyMessage = this._createBridgeReadyMessage();

    this._sendMessage(bridgeReadyMessage);
  }

  sendPageStatusMessage(page, loaded) {
    const pageMessage = this._createPageStatusMessage(page, loaded);

    this._sendMessage(pageMessage);
  }

  _mapChannel({name, payload}) {
    return {
      channelName: name,
      channelPayload: payload
    };
  }

  resetChannels() {
    console.log(`CellsNativeManager::resetChannels()`);
    const resetChannelsMessage = this._createResetChannelsMessage();

    this._sendMessage(resetChannelsMessage);
  }

  _createBackStepMessage() {
    const { BACK_STEP } = OPERATIONS.HTML;

    return {
      op: BACK_STEP,
      payload: {}
    };
  }

  _createPageStatusMessage(pageName, loaded) {
    return {
      op: loaded ? OPERATIONS.HTML.PAGE_LOADED : OPERATIONS.HTML.PAGE_UNLOADED,
      payload: {
        page: pageName
      }
    };
  }

  _createNavigationMessage(pageName, pageParams, replace) {
    const { NAVIGATION, NAVIGATION_AND_REPLACE } = OPERATIONS.HTML;

    return {
      op: replace ? NAVIGATION_AND_REPLACE : NAVIGATION,
      payload: {
        page: pageName,
        params: pageParams
      }
    };
  }

  _createSyncChannelsDataMessage(channelsData) {
    const { SYNC_CHANNELS_DATA } = OPERATIONS;

    return {
      op: SYNC_CHANNELS_DATA,
      payloadChannel: channelsData
    };
  }

  _createBridgeReadyMessage() {
    const { BRIDGE_READY } = OPERATIONS.HTML;

    return {
      op: BRIDGE_READY,
      payload: {}
    };
  }

  _createResetChannelsMessage() {
    const { RESET_CHANNELS_DATA } = OPERATIONS;

    return {
      op: RESET_CHANNELS_DATA,
      payload: {}
    };
  }

  _sendMessage(message) {
    if (this.source) {
      this.source.postMessage(JSON.stringify(message));
    }
  }

  /**
   * Listener of native messages, use operationManager to execute operations
   * @param {Event} evt - event received from native
   */
  _processMessage(evt) {
    const rawMessage = this._parseMessage(evt.data);
    const { operation, payload } = this._mapMessage(rawMessage);

    this.operationManager.execute(operation, payload);
  }

  _parseMessage(data) {
    let parsedData = {};

    try {
      parsedData = (typeof data === 'string') ? JSON.parse(data) : {};
    } catch(error) {
      console.error(`Cannot parse native message '${data}'\n\t${error.message}`);
    }

    return parsedData;
  }

  _mapMessage(rawMessage) {
    const { op, payload } = rawMessage;

    return {
      operation: op,
      payload
    };
  }
}

export default CellsNativeManager;
