import CellsPolymerBridge from '../polymer/bridge';
import eventManager from '../core/manager/events';

import CellsNativeComponentConnector from './component-connector';
import CellsNativeManager from './native-manager';
import CellsNativeRouter from './router';
import CellsNativeTemplateManager from './manager/template';
import NativeBridgeChannelManager from './manager/bridge-channels';

import CoreConstants from '../core/constants';

import { EVENTS, PREFIX } from './constants';

const { bindingCodes, externalEventsCodes } = CoreConstants;

const PRIVATE_EVENTS = EVENTS.PRIVATE;
const WAIT_UNTIL_NEXT_LOGOUT = 5000;

/**
 * Class to create cells-native-bridge component
 *
 * @extends CellsPolymerBridge
 */
let CellsNativeBridge = class CellsNativeBridge extends CellsPolymerBridge {
  _initialRouteHandled = false;
  _shouldInitPrivateEvents = false;

  constructor(config = {}) {
    console.log('CellsNativeBridge()');

    super(config);

    this._initEvents();
    this._initComponentConnector();

    this._doPreRender(config);
    this._notifyBridgeReady();
    this._lastLogoutTime = 0;
  }

  _initDependencies(dependencies) {
    console.log('CellsNativeBridge::_initDependencies()');

    dependencies = Object.assign({
      NativeManager: CellsNativeManager,
      ComponentConnector: CellsNativeComponentConnector,
      Router: CellsNativeRouter,
      TemplateManager: CellsNativeTemplateManager,
      BridgeChannelManager: NativeBridgeChannelManager
    }, dependencies);

    super._initDependencies(dependencies);
  }

  publish(channelName, value, options = {}) {
    super.publish(channelName, value);

    const { forwardToNative } = this._parsePublishOptions(options);

    if (forwardToNative) {
      this._handleSyncDataToNative({ detail: channelName });
    }
  }

  _parsePublishOptions({
    forwardToNative = false,
  }) {
    return {
      forwardToNative,
    };
  }

  navigateToNative(page, params) {
    const { NATIVE_NAVIGATION_REQUEST } = EVENTS;

    eventManager.emit(NATIVE_NAVIGATION_REQUEST, {
      event: {},
      detail: { page, params }
    });
  }

  backStep() {
    const { ROUTER_BACKSTEP } = externalEventsCodes;

    eventManager.emit(ROUTER_BACKSTEP, {
      event: {},
      detail: {}
    });
  }

  resetRouter() {
    eventManager.removeListener('nav-request', this.navRequestListener);
    this.Router = new CellsNativeRouter(this);

    this.Router.handler = () => this.routeHandler();
    this.Router.addRoutes(this.routes);
    this._initSkipNavigations();
    this.Router.start();
    this.Router.hashIsDirty = false;
    eventManager.on('nav-request', this.navRequestListener);
  }

  routeHandler() {
    super.routeHandler();

    if (!this._initialRouteHandled) {
      const { name, params } = this.Router.currentRoute;
      const route = this.Router.createRoute(name, params);

      this.handleForward(route);

      this._initialRouteHandled = true;

      console.log('CellsNativeBridge::routeHandler()', route);
    }
  }

  _cacheSelected() {
    this.TemplateManager.cacheSelected();
    this.Router.currentRoute = this.Router.navigationStack.createRoute(PREFIX.NATIVE_ROUTE + 'init');
  }

  resetWebView() {
    this._shouldInitPrivateEvents = true;
    this.Router.init();
    //this.TemplateManager.cacheSelected();
    this._cacheSelected();
    this._resetBridgeChannels();
    this._removeTemplates();
    this._resetEventSubscriptions();
  }

  canDoLogout() {
    var now = new Date().getTime();
    var elapsedMillisecondsSinceLastTimeLogout = this._lastLogoutTime > 0 ? now - this._lastLogoutTime : now;
    return elapsedMillisecondsSinceLastTimeLogout > WAIT_UNTIL_NEXT_LOGOUT;
  }

  logoutDueToNativeRequest() {
    if (this.canDoLogout()) {
      this._lastLogoutTime = new Date().getTime();
      this._resetBridgeChannels();
      this._removeTemplates();
      this.TemplateManager.selected = "";
      this._resetEventSubscriptions();

      this.Router.invalidateLocationHash();
      this.Router.hashIsDirty = true;
      this._initialRouteHandled = false;
      this._shouldInitPrivateEvents = false;
      this._initComponentConnector();
      //this.Router.init();
      if (this.preRenderConfig) {
        this._preRender(this.preRenderConfig, false);
      }
      this.resetRouter();
    }
  }

  logout() {

    let initialPos = this.initialTemplate.indexOf(PREFIX.NATIVE_ROUTE);
    let goToNative = initialPos === 0;
    let initialTemplate = goToNative ? this.initialTemplate.substring(PREFIX.NATIVE_ROUTE.length) : this.initialTemplate;

    this._resetBridgeChannels();
    this._removeTemplates();
    this.Router.init();

    if(goToNative) {
      let route = {
        page: initialTemplate,
        options: {
          cleanNavigationStack: true
        }
      };
      this._shouldInitPrivateEvents = true;
      this.handleForward(route, false, true);
      this.NativeManager.sendNavigationMessage(route.page, {}, true);
      //this.TemplateManager.cacheSelected();
      this._cacheSelected();
    } else {
      this.Router.go(initialTemplate);
    }
    this._resetEventSubscriptions();
    this._initComponentConnector();
  }

  _removeTemplates() {
    this.TemplateManager.removeTemplateChildrens(this.crossContainerId);
    this.TemplateManager.removeTemplates(this.initialTemplate, this.crossContainerId);
    this.TemplateManager.removeTemplate(this.initialTemplate);
  }

  _resetEventSubscriptions() {
    this._addInitialSubscribersToEvents();
    //this.ComponentConnector.handleI18n();
  }

  _onConfigChange(evt) {
    if (window.AppConfig && evt.detail) {
      for (let prop in evt.detail) {
        if(evt.detail.hasOwnProperty(prop)){
          if ('config' === prop) {
            window.AppConfig.app = evt.detail[prop];
          } else {
            window.AppConfig[prop] = evt.detail[prop];
          }
        }
      }
      this.PageManager.storage.clear();
      this._removeTemplates();
      this.routeHandler();
    }
  }

  _onLocaleChange(evt) {
    if (window.I18nMsg && evt.detail && evt.detail.lang) {
      window.I18nMsg.lang = evt.detail.lang;
    }
  }

  _initEvents() {
    // sync channels
    eventManager.on(EVENTS.SYNC_CHANNELS_DATA, (data) => this._handleNativeSyncChannelsData(data));

    // HTML navigation
    eventManager.on(EVENTS.ROUTER_NAVIGATION, (info) => this._handleNavigationRequest(info));
    eventManager.on(EVENTS.ROUTER_BACKSTEP, (info) => this._handleBackRequest(info));
    eventManager.on(EVENTS.RESET_WEBVIEW, () => this.resetWebView());
    eventManager.on(EVENTS.NATIVE_LOGOUT, () => this.logoutDueToNativeRequest());
    eventManager.on(EVENTS.UPDATE_SKIP_NAVIGATIONS, (info) => this.updateSkipNavigations(info));

    // Internal Navigation
    eventManager.on(EVENTS.INTERNAL_ROUTER_NAVIGATION, (info) => this._handleNavigationStack(info));
    eventManager.on(EVENTS.INTERNAL_ROUTER_BACKSTEP, () => this.handleBack({type: EVENTS.INTERNAL_ROUTER_BACKSTEP}));
    eventManager.on(EVENTS.INTERNAL_ROUTER_BACKSTEP_FROM_NATIVE, (info) => this._handleBackRequestFromNative(info));

    // NATIVE navigation
    eventManager.on(EVENTS.NATIVE_NAVIGATION_REQUEST, (info) => this._handleNativeNavRequest(info));
    eventManager.on(EVENTS.NATIVE_BACKSTEP_REQUEST, () => this._handleNativeBack());

    // NATIVE INTEROP
    eventManager.on(EVENTS.NATIVE_CHANNEL_SYNC, (info) => this._handleSyncDataToNative(info));
  }

  _initComponentConnector() {
    this.ComponentConnector.handleI18n(this._onLocaleChange.bind(this));
    this.ComponentConnector.handleConfig(this._onConfigChange.bind(this));
    this.ComponentConnector.handleAppContextChange(this._onAppContextChanged.bind(this));
  }

  _doPreRender(config) {
    if (config.initialPreRender) {
      const preRenderConfig = {
        pages: config.initialPreRender
      };
      this.preRenderConfig = preRenderConfig;
      this._preRender(preRenderConfig, false);
    }
  }

  _onAppContextChanged(evt) {
    const contextInfo = evt.detail.value;

    if (contextInfo.fromPage) {
      this.NativeManager.sendPageStatusMessage(contextInfo.currentPage, true);
      this.NativeManager.sendPageStatusMessage(contextInfo.fromPage, false);
    }
  }

  _notifyBridgeReady() {
    this.NativeManager.sendBridgeReadyMessage();
  }

  /**
   * @private
   * @emits INTERNAL_ROUTER_BACKSTEP
   */
  _handleBackRequest(detail = {}) {
    console.log('CellsNativeBridge::_handleBackRequest()');
    const route = this.Router.getLastRoute();

    /** FIXME: Workaround to manage Android back.
     * To get Android 'back' message from Native to HTML is needed
     * to manage this flag to avoid doing back when app is resumed.
    */
    if (!detail.willAppear || this.Router.isNativeRoute(route)) {
      eventManager.emit(EVENTS.INTERNAL_ROUTER_BACKSTEP);
    }
  }

  registerCurrentTemplate(currentTemplate, previousTemplate) {
    const initialRoute = PREFIX.NATIVE_ROUTE + 'init';
    const { TEMPLATE_REGISTERED } = externalEventsCodes;
    const { UI } = bindingCodes;
    let options = this;

    if (this._isNewStateIsEqualToOldState(currentTemplate.name, (previousTemplate||{}).name)) {
      return;
    }
    /*
    console.log('registerCurrentTemplate COMPARE', this.appState);
    const initialRoute = PREFIX.NATIVE_ROUTE + 'init';
    const currentTemplateName = currentTemplate.name;
    const previousTemplateName = (previousTemplate||{}).name;

    const previousTemplateIsInitialPage = previousTemplateName === initialRoute || previousTemplateName === undefined;
    const previousStatePageWasInitialPage = (this.appState||{}).previousPage === initialRoute || (this.appState||{}).previousPage === undefined;

    if (this.appState &&
        this.appState.currentPage === currentTemplateName
        && (this.appState.previousPage === previousTemplateName || (previousStatePageWasInitialPage===previousTemplateIsInitialPage)) ) {
      console.log('registerCurrentTemplate skip', currentTemplateName, previousTemplateName);
      return;
    }

    this.appState = { currentPage: currentTemplateName, previousPage: previousTemplateName };
    */

    if (previousTemplate && previousTemplate !== currentTemplate && initialRoute !== previousTemplate.name) {
      this.unregisterChildren(previousTemplate, options.binding === UI ? 'DM' : null);
    }
    this.registerChildren(currentTemplate, options.binding === UI ? 'DM' : null);

    this._updateChannels(previousTemplate, currentTemplate);
    eventManager.emit(TEMPLATE_REGISTERED, { template: currentTemplate.name });
  }

  _handleBackRequestFromNative(detail) {
    console.log('CellsNativeBridge::_handleBackRequestFromNative()');
    const pageName = detail.page;

    this.TemplateManager.selectTemplate(pageName);
  }

  /**
   * @private
   * @emits INTERNAL_ROUTER_NAVIGATION
   */
  _handleNavigationRequest(detail) {
    const pageName = detail.page;

    console.log('_handleNavigationRequest : to ', pageName);

    const event = new CustomEvent(PRIVATE_EVENTS.ROUTER_NAVIGATION, {
      detail: this._prepareNavigationParams(detail),
      bubbles: true, composed: true
    });

    if (this._shouldInitPrivateEvents) {
      this._shouldInitPrivateEvents = false;
      this.routeHandler();
    }
    this.TemplateManager.selectTemplate(pageName);
    eventManager.emit(EVENTS.INTERNAL_ROUTER_NAVIGATION, { detail, event });
    console.log(`_handleNavigationRequest : to NATIVE : ${pageName}`);
  }

  _prepareNavigationParams(detail = {}) {
    const result = Object.assign({}, detail);
    for (let key in result.params) {
      if (result.params.hasOwnProperty(key)) {
        result[key] = result.params[key];
        result.params[key] = key;
      }
    }
    return result;
  }

  _handleNavigationStack(info) {
    console.log("INFO ", info);
    const route = this._unflattenRouteFromEvent(info); //@TODO PLACE INTO ROUTER
    const options = info.detail.options || {};
    console.log('_handleNavigationStack options.skipHistory :', options.skipHistory);
    console.log('_handleNavigationStack :from', this.Router.getLastRoute().page, route.page);

    if (options.skipHistory === true || options.skipHistory === false) {
      this.Router.addSkipNavigation(
        { from        : info.detail.page,
          to          : this.Router.getLastRoute().page,
          skipHistory : options.skipHistory
        }
      );
    }

    //this.Router.manageStack(route, route.replace);
    this.handleForward(route, route.replace);
  }

  _isNewStateIsEqualToOldState(currentTemplateName, previousTemplateName) {
    console.log('_isNewStateIsEqualToOldState COMPARE', this.appState);
    const initialRoute = PREFIX.NATIVE_ROUTE + 'init';
    //const currentTemplateName = currentTemplate.name;
    //const previousTemplateName = (previousTemplate||{}).name;

    const previousTemplateIsInitialPage = previousTemplateName === initialRoute || previousTemplateName === undefined;
    const previousStatePageWasInitialPage = (this.appState||{}).previousPage === initialRoute || (this.appState||{}).previousPage === undefined;

    const isSameState = (this.appState &&
        this.appState.currentPage === currentTemplateName
        && (this.appState.previousPage === previousTemplateName || (previousStatePageWasInitialPage===previousTemplateIsInitialPage)));
    console.log('_isNewStateIsEqualToOldState skip=', isSameState);
    return isSameState;
  }

  // this function MAY BE OVERRIDE on native bridge - not required due to internal router behavior
  // BUT it's required to trigger updateAppContext
  _updateChannels(previousTemplate, currentTemplate) {
    if (this.BridgeChannelManager) {
      const oldName = previousTemplate ? previousTemplate.name : undefined;
      const newName = currentTemplate.name;
      const navigation = { from: { page: oldName }, to: {page: newName} };
      const privateChannelsAlreadyInitialized = this.shouldInitPrivateEvents(navigation);

      if (!privateChannelsAlreadyInitialized && oldName !== newName) {
        this.BridgeChannelManager.initPrivateChannel(oldName, newName);
        this.BridgeChannelManager.updateAppContext(oldName, newName);
      }
    }
  }

  shouldInitPrivateEvents(navigation) {
    return this.Router.isNativeRoute(navigation.from) || this.Router.isNativeRoute(navigation.to);
  }

  updatePrivateChannels(oldRoute, newRoute) {
    if (this.BridgeChannelManager) {
      var oldPage = oldRoute ? oldRoute.page : undefined;

      this.BridgeChannelManager.updateAppContext(oldPage, newRoute.page);
      this.BridgeChannelManager.initPrivateChannel(oldPage, newRoute.page);
    }
  }

  handlePrivateChannelsForNavigation(navigation) {
    if (this.shouldInitPrivateEvents(navigation)) {
      this.updatePrivateChannels(navigation.from, navigation.to);
    }
  }

  /**
   * This method is executed before a backward navigation so it can be used to
   * decide if the navigation can continue or must be cancelled. So it's a hook
   * that applications can override. The default implementation always return { continue: true }
   *
   * @param {Object} backNavigation an object representing the backward navigation with the properties:
   *   - currentPage
   *   - backToPage
   *
   * @return {Object} an object that must contain the property continue with a boolean value:
   * if continue is true, the bridge will perfom the backward navigation;
   * otherwise it will cancel the navigation and the returned object will be
   * published in the channel __bridge_cancelled_back_navigation
   *
   */
  onNativeBackStepRequest(/*backNavigation*/) {
    return { continue: true };
  }

  /**
   * Execution of the hook onBackStepRequest wrapped in a promise
   *
   * @param {Object} backNavigation an object representing the backward navigation with the properties:
   *   - currentPage
   *   - backToPage
   * It will be passed to the hook function onBackStepRequest
   *
   * @return {Promise} a promise with the result of the execution of the hook onBackStepRequest
   */
  _doBeforeBack(backNavigation) {
    return Promise.resolve(this.onNativeBackStepRequest(backNavigation));
  }

  goBack() {
    const navigation = super.goBack();
    this.handlePrivateChannelsForNavigation(navigation);
    return navigation;
  }

  /**
   * This method is executed when the event router-backstep is fired.
   * It calls the hook method for handling backward navigations and if that method
   * allows the continuation, it does the navegation. Otherwise it will cancel the navigation
   * and publish the response in the channel __bridge_cancelled_back_navigation.
   */
  handleBack(evt) {
    const lastNavigation = this.Router.navigationStack.lastNavigation();
    const backNavigation = { currentPage: lastNavigation.to, backToPage: lastNavigation.from };
    if (evt.type === EVENTS.INTERNAL_ROUTER_BACKSTEP) {
      this._doBeforeBack(backNavigation).then(result => {
        if (result.continue === true) {
          this.goBack();
        } else {
          result.cancelledNavigation = backNavigation;
          this.BridgeChannelManager.publishCancelledBackNavigation(result);
        }
      });
    } else {
      this.goBack();
    }
  }

  handleForward(route, replace = false, native = false) {
    const navigation = this.Router.manageStack(route, replace, native);
    this.handlePrivateChannelsForNavigation(navigation);
  }

  /**
   * Unflattes route from given event object.
   * @private
   * @param  {Object} info Event info.
   * @return {Object}      Unflatted route.
   */
  _unflattenRouteFromEvent(info) {
    const { detail } = info;
    const { page, params, cleanNavigationStack = false, cleanUntil = false, replace = false } = detail;
    const paramsValues = info.event.detail;
    const options = {};
    const route = {
      page,
      params: this._unflattenRouteParams(params, paramsValues, true),
      replace
    };

    if (cleanNavigationStack) {
      options.cleanNavigationStack = cleanNavigationStack;
    }

    if (cleanUntil) {
      options.cleanUntil = cleanUntil;
    }

    if (Object.keys(options).length > 0) {
      route.options = options;
    }

    return route;
  }

  /**
   * Unflattes route params based on given param object list and given param values.
   * @private
   * @param   {Object}  [list={}]                     Object including params names on object keys.
   * @param   {Object}  [values={}]                   Object containing key/value properties with params values.
   * @param   {Boolean} [swapParamsDefinition=false]  Flag indicating if final params object should gather property names based on value property of list params.
   * @return {Object}                                 Object containing unflatten params based on list & values.
   */
  _unflattenRouteParams(list = {}, values = {}, swapParamsDefinition = false) {
    const params = {};

    Object.keys(list).forEach((param) => {
      const targetProperty = swapParamsDefinition ? list[param] : param;
      const valueProperty = values[param];

      if (valueProperty) {
        params[targetProperty] = valueProperty;
      } else {
        console.error(`Error trying to unflat value from param ${param} - value is not presented on value list (${JSON.stringify(values)})`);
      }
    });

    return params;
  }

  _handleSyncDataToNative(info) {
    const channelName = info.detail;
    const channel = this.ComponentConnector.getChannel(channelName);

    this.NativeManager.sendSyncChannelsDataMessage([channel]);
  }

  _handleNativeNavRequest(info) {
    console.log('CellsNativeBridge::_handleNativeNavRequest()');

    const route = this._unflattenNavigationData(info);
    const resetChannels = route.resetChannels || false;

    if (!resetChannels) {
      this._syncChannelsToNative();
    } else {
      this.NativeManager.resetChannels();
    }

    //this.Router.manageStack(route, false, true);
    this.handleForward(route, false, true);
    this.NativeManager.sendNavigationMessage(route.page, route.params);
    this.TemplateManager.cacheSelected();
    //this._cacheSelected();
  }

  _unflattenNavigationData(info) { //@TODO REFACTOR SPR - _unflattenRouteFromEvent params & options
    const eventPayload = info.event.detail;

    const route = Object.assign({}, info.detail);
    const { cleanNavigationStack = false, cleanUntil = false } = route;
    const routeParams = route.params;
    const options = {};

    const pageParams = this._unflattenRouteParams(routeParams, eventPayload, true);

    if (pageParams) {
      route.params = pageParams;
    }

    if (cleanNavigationStack) {
      options.cleanNavigationStack = cleanNavigationStack;
    }

    if (cleanUntil) {
      options.cleanUntil = cleanUntil;
    }

    if (Object.keys(options).length > 0) {
      route.options = options;
    }

    return route;
  }

  _handleNativeBack() {
    console.log('CellsNativeBridge::_handleNativeBack()');
    this._syncChannelsToNative();
    this.NativeManager.sendBackstepMessage();
    this._cacheSelected();
  }

  updateSkipNavigations(info) {
    const navigations = info.navigations;
    navigations.forEach(nav => {
      this.Router.addSkipNavigation(nav);
    });
  }

  _handleNativeSyncChannelsData(payload) {
    console.log('CellsNativeBridge::_handleNativeSyncChannelsData()');
    this.ComponentConnector.syncChannels(payload.channels);
  }

  _syncChannelsToNative() {
    const channels = this.ComponentConnector.getChannels();
    this.NativeManager.sendSyncChannelsDataMessage(channels);
  }
}

export default CellsNativeBridge;
