/**
 * @constant
 * @desc Operations between NATIVE and HTML bridges
 * @type {Object}
 * @property {String}  SYNC_CHANNELS_DATA         - Sync channels
 * @property {String}  RESET_CHANNELS_DATA        - Reset channels
 * @property {Object}  NATIVE                     - From NATIVE TO HTML
 * @property {String}  NATIVE.BACK_STEP           - Back
 * @property {String}  NATIVE.NAVIGATION          - Navigate
 * @property {String}  NATIVE.RESET_WEBVIEW       - Performs a Logout
 * @property {Object}  HTML                       - From HTML to NATIVE
 * @property {String}  HTML.BACK_STEP             - Back
 * @property {String}  HTML.NAVIGATION            - Navigate
 */
export const OPERATIONS = {
  SYNC_CHANNELS_DATA: 'channels',
  RESET_CHANNELS_DATA: 'reset-channels',
  NATIVE: {
    BACK_STEP: 'back',
    NAVIGATION: 'nav',
    RESET_WEBVIEW: 'reset-webview',
    LOGOUT: 'logout',
    UPDATE_SKIP_NAVIGATIONS: 'skip-nav'
  },
  HTML: {
    BACK_STEP: 'pop',
    NAVIGATION: 'push',
    NAVIGATION_AND_REPLACE: 'popAndPush',
    BRIDGE_READY: 'bridgeReady',
    PAGE_LOADED: 'pageLoaded',
    PAGE_UNLOADED: 'pageUnloaded'
  }
};

export const EVENTS = {
  /**
   * HTML -> Native channel sync
   * @event NATIVE_CHANNEL_SYNC
   */
  NATIVE_CHANNEL_SYNC: 'native-channel-sync',
  /**
   * Native back navigation request event.
   * @event NATIVE_BACKSTEP_REQUEST
   */
  NATIVE_BACKSTEP_REQUEST: 'native-backstep-request',
  /**
   * Native navigation request event.
   * @event NATIVE_NAVIGATION_REQUEST
   * @type {object}
   * @property {Event} event - Indicates the page to navigate.
   * @property {Object} detail - Indicates the page to navigate.
   * @property {String} detail.page - Indicates the page to navigate.
   */
  NATIVE_NAVIGATION_REQUEST: 'native-nav-request',
  /**
   * Internal cells-bridge navigation event.
   * @event INTERNAL_ROUTER_NAVIGATION
   * @type {object}
   * @property {Event} event - Indicates the page to navigate.
   * @property {Object} detail - Indicates the page to navigate.
   * @property {String} detail.page - Indicates the page to navigate.
   */
  INTERNAL_ROUTER_NAVIGATION: 'nav-request',
  /**
   * Internal cells-native-bridge back navigation request event.
   * @event INTERNAL_ROUTER_BACKSTEP
   * @type {object}
   * @property {String} page - Indicates the page to navigate.
   */
  INTERNAL_ROUTER_BACKSTEP: 'backstep',
  /**
   * Internal cells-native-bridge back navigation request event from native page.
   * @event INTERNAL_ROUTER_BACKSTEP_FROM_NATIVE
   * @type {object}
   * @property {String} page - Indicates the page to navigate.
   */
  INTERNAL_ROUTER_BACKSTEP_FROM_NATIVE: 'backstep-from-native',
  /**
   * Back Request from native event
   * @event ROUTER_BACKSTEP
   * @type {object}
    * @property {Boolean} paylwillAppearoad - Androis flag Web will be shown.
   */
  ROUTER_BACKSTEP: 'backstep-request',
  /**
   * Navigation request from native event.
   * @event ROUTER_NAVIGATION
   * @type {object}
   * @property {String} page - Name of the page to natigate.
   */
  ROUTER_NAVIGATION: 'external-nav-request',
  /**
   * Request from native event to reset the web view.
   * @event RESET_WEBVIEW
   */
  RESET_WEBVIEW: 'reset-webview',
  /**
   *  request from native event.
   * @event NATIVE_LOGOUT
   */
  NATIVE_LOGOUT: 'native-logout',
  /**
   *  request from native event.
   * @event UPDATE_SKIP_NAVIGATIONS
   */
  UPDATE_SKIP_NAVIGATIONS: 'update-skip-navigations',
  /**
   * Channels syncronization from native event.
   * @event SYNC_CHANNELS_DATA
   * @type {object}
   * @property {Channel[]} channels - Array of channels to sync.
   */
  SYNC_CHANNELS_DATA: 'sync-channels-data',
  PRIVATE: {
    SYNC_CHANNELS_DATA: '__cells-data-sync',
    ROUTER_NAVIGATION: '__cells-nav-request'
  }
};

/**
 * @constant
 * @desc Keys of declared Objects
 * @type {Object}
 * @property {String}  NATIVE_CHANNEL - Key of the native connector
 */
export const KEYS = {
  NATIVE_CHANNEL: 'NativeBridge',
  NATIVE_PLUGIN_CHANNEL: 'PluginBridge'
};

/**
 * @constant
 * @desc Prefixes used in cells-native-bridge
 * @type {Object}
 * @property {String}  NATIVE_ROUTE - Prefix of native routes in Routers
 * @property {String}  PRIVATE_CHANNEL - Prefix of private channels in ComponentConnector
 */
export const PREFIX = {
  NATIVE_ROUTE: 'cells-native-route:',
  PRIVATE_CHANNEL: '__bridge_'
};

export const PLUGIN_RESPONSE_STATUS = {
  SUCCESS: 0,
  ERROR: 1
};

export const PLUGIN_RESPONSE_CALLBACKS = {
  SUCCESS_ACCESSOR: 'success',
  FAIL_ACCESSOR: 'fail'
};

export default {
  OPERATIONS,
  EVENTS,
  KEYS,
  PREFIX,
  PLUGIN_RESPONSE_STATUS,
  PLUGIN_RESPONSE_CALLBACKS
}

/**
 * @typedef {Object} Channel
 * @property {String} name - The channel name
 * @property {any} payload - The channel content
 */

/**
 * @typedef {Object} Link
 * @property {Link} page - The page name
 */

/**
 * @typedef {Object} Connection
 * @property {Link} link - The web link definition
 * @property {Link} nativeLink - The native link definition
 */
